  <article id="detail">
    <div class="title">
      <span class="flag">
        <img
          v-if="props.national.flag_img"
          :src="props.national.flag_img"
          :alt="`${props.national.country_nm} 국기`"
        />
      </span>
      <h2>
        <p>
          {{ props.national.country_nm }}
          <span>{{ props.national.country_eng_nm }}</span>
        </p>
        <p class="capital">
          <span>수도:</span>
          <span>{{ props.national.capital }}</span>
        </p>
      </h2>
    </div>
    <div class="content">
      <img
        v-if="props.national.map_img"
        class="map-img"
        :src="props.national.map_img"
        :alt="`${props.national.country_nm} 지도`"
      />
      <div class="text">
        <ul>
          <li v-if="props.national.area || props.national.area_desc">
            <span class="icon">&#127759;</span>
            <span class="head">면적:</span>
            <span class="body"
              >{{ props.national.area }}{{ props.national.area_desc }}</span
            >
          </li>
          <li v-if="props.national.climate">
            <span class="icon">&#127777;</span>
            <span class="head">기후:</span>
            <span class="body">{{ props.national.climate }}</span>
          </li>
          <li v-if="props.national.ethnic">
            <span class="icon">&#128125;</span>
            <span class="head">민족:</span>
            <span class="body">{{ props.national.ethnic }}</span>
          </li>
          <li v-if="props.national.population">
            <span class="icon">&#128101;</span>
            <span class="head">인구:</span>
            <span class="body"
              >{{ props.national.population
              }}{{ props.national.population_desc }}</span
            >
          </li>
          <li v-if="props.national.religion">
            <span class="icon">&#128330;</span>
            <span class="head">종교:</span>
            <span class="body">{{ props.national.religion }}</span>
          </li>
        </ul>
      </div>
    </div>
  </article>
</template>
