<script setup>
import axios from "axios";
import { ref } from "vue";
import Search from "./components/Search.vue";
import Detail from "./components/Detail.vue";

const national = ref({});
const searchWord = ref("");
const openSearch = ref(true);
const loading = ref(false);

const emitSearchText = (val) => {
  const regex = /^[ㄱ-ㅎ가-힣]+$/;
  if (!val) {
    window.alert("국가명을 입력해주세요.");
    return;
  }
  if (!regex.test(val)) {
    window.alert("한글로 입력해주세요.");
    return;
  }
  if (val === searchWord.value) {
    return;
  }
  searchWord.value = val;
  loadData(val);
};
const loadData = async (name) => {
  try {
    const sectionBg = document.getElementById("section");
    const url = "http://apis.data.go.kr/1262000";
    const serviceKey =
      "%2B4pVJGaV31U%2Bt9T20cC9ohs%2BZD%2FmqBa4%2FypH8N21OphUUs0ErGEEFJGaiHzEx632g7YprcB9UqPGFT7ygJ6XMQ%3D%3D";
    const basicUrl = `${url}/OverviewGnrlInfoService/getOverviewGnrlInfoList?serviceKey=${serviceKey}&cond[country_nm::EQ]=${name}`;
    const flagUrl = `${url}/CountryFlagService2/getCountryFlagList2?serviceKey=${serviceKey}&cond[country_nm::EQ]=${name}`;
    const mapUrl = `${url}/CountryMapService2/getCountryMapList2?serviceKey=${serviceKey}&cond[country_nm::EQ]=${name}`;
    const basicData = await axios.get(basicUrl);
    const flagData = await axios.get(flagUrl);
    const mapData = await axios.get(mapUrl);
    if (
      basicData.data.data[0] &&
      flagData.data.data[0] &&
      mapData.data.data[0]
    ) {
      loading.value = true;
      national.value = [];
      if (basicData.statusText === "OK") {
        national.value = basicData.data.data[0];
      }
      if (flagData.statusText === "OK") {
        national.value.flag_img = flagData.data.data[0].download_url;
        sectionBg.style.backgroundImage = `url(${national.value.flag_img})`;
      }
      if (mapData.statusText === "OK") {
        national.value.map_img = mapData.data.data[0].download_url;
      }
      setTimeout(() => {
        openSearch.value = false;
        loading.value = false;
      }, 500);
    } else {
      national.value = [];
      openSearch.value = true;
      sectionBg.style.backgroundImage = "";
      sectionBg.style.backgroundColor = "#fff";
      window.alert("Oops! No data");
    }
  } catch (err) {
    console.error(err);
  }
};
</script>
<template>
  <section id="section" :class="openSearch ? 'on' : 'off'">
    <div class="outer">
      <div class="max-width">
        <article id="intro">
          <h3>여행지 정보 검색 서비스</h3>
          <img class="logo" src="./assets/wayout_logo.png" alt="wayout logo" />
        </article>
        <Search @emitSearchText="emitSearchText" />
        <Detail v-if="!openSearch" :national="national" />
      </div>
    </div>
    <div v-if="loading" class="loading">
      <p>
        <span class="material-symbols-outlined"> clock_loader_10 </span>
      </p>
    </div>
  </section>
</template>
<style lang="scss">
@import "./css/index";
</style>
