<script setup>
import { ref, defineEmits } from "vue";
const emits = defineEmits(["emitSearchText"]);

const searchText = ref("");
const emitText = (e) => {
  if (window.event.keyCode == 13 || e) {
    emits("emitSearchText", searchText.value);
  }
};
</script>
<template>
  <article id="search">
    <div class="search-box">
      <input
        type="text"
        @keyup.enter="emitText()"
        v-model="searchText"
        placeholder="국가명을 입력해주세요."
      />
      <button @click="emitText(true)" class="search-btn">
        <span class="material-symbols-outlined">arrow_forward</span>
      </button>
    </div>
    <ul>
      <li>*국가명은 한글로 입력해주세요.</li>
      <li>*대한민국, 북한은 검색 제외 대상입니다.</li>
    </ul>
  </article>
</template>
